@charset "UTF-8";

@font-face {
  font-family: 'gobcl';
  src: url($fontsPath + 'gobcl.eot');
  src: url($fontsPath + 'gobcl.eot?#iefix') format('embedded-opentype'),
  url($fontsPath + 'gobcl.woff') format('woff'),
  url($fontsPath + 'gobcl.ttf') format('truetype'),
  url($fontsPath + 'gobcl.svg#gobcl') format('svg');
  font-weight: normal;
  font-style: normal;

}

[data-icon]:before {
  font-family: 'gobcl' !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^='ic-']:before,
[class*=' ic-']:before {
  font-family: 'gobcl' !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

